
import { defineComponent, computed } from 'vue'

export default defineComponent({
  name: '',
  props: {
    leave: {
      type: Number,
      required: true,
      default: 0
    }
  },
  components: {},
  setup (props) {
    const starLeave = computed(() => {
      return props.leave
    })
    return {
      starLeave
    }
  }
})
