

import { defineComponent, PropType, ref, watch } from 'vue'
import StButton from '@/components/StButton.vue'
import userDomCreate from '@/hook/userDomCreate'
interface AfterClose {
  cancel: boolean;
  confirm: boolean;
}
export interface Confirm {
  title?: string;
  content?: string;
  confirmText?: string;
  cancelText?: string;
  confirm: () => void;
  cancel: () => void;
  destroy: () => void;
  showClose?:boolean
  afterClose: (params: AfterClose) => void;
}

export default defineComponent({
  name: 'Confirm',
  components: { StButton },
  props: {
    show: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    title: {
      type: String as PropType<string>,
      default: 'Prompt'
    },
    content: {
      type: String as PropType<string>,
      default: 'This action will permanently delete the file. Do you want to continue?'
    },
    confirmText: {
      type: String as PropType<string>,
      default: 'Confirm'
    },
    cancelText: {
      type: String as PropType<string>,
      default: 'Cancel'
    },
    showClose: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    confirm: {
      type: Function as PropType<() => void>,
      default () {
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        return () => {}
      }
    },
    cancel: {
      type: Function as PropType<() => void>,
      default () {
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        return () => {}
      }
    },
    destroy: {
      type: Function as PropType<() => void>,
      default () {
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        return () => {}
      }
    },
    afterClose: {
      type: Function as PropType<() => void>,
      default () {
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        return () => {}
      }
    }
  },
  setup (props: Readonly<Confirm>) {
    userDomCreate('Confirm')
    const removeChild = () => {
      const node = document.getElementById('Confirm')
      if (node) {
        document.body.removeChild(node)
      }
    }

    const handleClick = (index: number) => {
      if (index === 0) {
        removeChild()
        props.cancel()
      } else {
        removeChild()
        props.confirm()
      }
    }
    const handleStopPropagation = (e: MouseEvent) => {
      e.stopPropagation()
    }
    return {

      handleClick,
      handleStopPropagation
    }
  }
})
