
import { defineComponent, reactive, PropType } from 'vue'
import userDomCreate from '@/hook/userDomCreate'
export type MessageType = 'success' | 'warning' | 'default' | 'error';
export default defineComponent({
  name: '',
  props: {
    type: {
      type: String as PropType<MessageType>,
      default: 'default'
    },
    message: {
      type: String,
      default: ''
    },
    showClose: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  setup () {
    const meType = reactive({
      success: {
        color: '#67C23A',
        borderColor: '#e1f3d8',
        background: '#f0f9eb'
      },
      warning: {
        color: '#E6A23C',
        borderColor: '#faecd8',
        background: '#fdf6ec'
      },
      default: {
        color: '#909399',
        borderColor: '#EBEEF5',
        background: '#edf2fc'
      },
      error: {
        color: '#F56C6C',
        borderColor: '#fde2e2',
        background: '#fef0f0'
      }

    })
    userDomCreate('message')
    return {
      meType
    }
  }
})
