
import { defineComponent, reactive, toRefs, SetupContext } from 'vue'
export default defineComponent({
  name: '',
  props: {
    label: {
      type: String,
      reqrired: true
    }
  },
  components: {},
  setup (props, context) {
    const data = reactive({})
    return {
      ...toRefs(data)
    }
  }
})
