
import { defineComponent, computed, ref } from 'vue'

export default defineComponent({
  name: '',
  props: {
    bannerList: {
      type: Array,
      required: true
    }
  },
  components: {},
  setup (props, context) {
    const imageList = computed(() => props.bannerList)
    const currentIndex = ref(0)
    const Timer = ref()
    const setTimer = () => {
      Timer.value = setInterval(() => {
        if (currentIndex.value === props.bannerList.length - 1) {
          currentIndex.value = 0
        } else {
          currentIndex.value++
        }
      }, 2000)
    }
    const clearTimer = () => {
      window.clearInterval(Timer.value)
    }
    setTimer()
    const bannerToggle = (i: number) => {
      if (i && currentIndex.value < props.bannerList.length - 1) {
        currentIndex.value++
      } else if (!i && i !== currentIndex.value) {
        currentIndex.value--
      }
    }
    const timerToggle = (i: number) => {
      i ? clearTimer() : setTimer()
    }
    const indexToggle = (i: number) => {
      currentIndex.value = i
    }
    return {
      imageList,
      Timer,
      currentIndex,
      bannerToggle,
      timerToggle,
      indexToggle
    }
  }
})
